  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.modal {
  top: 0;
  left: 0;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.25);
}

.modal_content {
  background-color: white;
  position: absolute;
  margin: 0 auto;
  width: 90%;
  height: 100%;
  max-width: 768px;
  overflow: scroll;
  padding: 20px;
  border-radius: 5px;
  border-color: red;
  border-width: 10px;
}

.close {
  color: Black;
  float: right;
  width: 20px;
  height: 20px;
  font-size: 32px;
  margin-right: 16px;
}

.close:hover {
  color: pink;
  cursor: pointer;
}

.input {
